import './App.css';
import {Business, Crypto, Design, DesignImage, TabImage, MobImage, Photography, Shape} from './Design/export';
import CustomCard from './Components/CustomCardContainer';
import Cards from './Components/Cards';
import { useEffect, useState } from 'react';

function App() {
  let srcImage;
  let [width, setWidth] = useState(window.innerWidth);
  useEffect(() => {
    let handleResize = () => {
      if(width != window.innerWidth) {
        setWidth(window.innerWidth);
      }
    }
    window.addEventListener('resize', handleResize);
  });
  if(width < 957 && width > 630) {
    srcImage = TabImage;
  } else if (width < 630) {
    srcImage = MobImage;
  } else {
    srcImage = DesignImage;
  }
  return (
    <div className="app">
      <div className='app__navbar'>
        <p>skilled</p>
        <button type='button'>Get Started</button>
      </div>
      <div className='app__header'>
        <div className='app__header-text'>
          <h1>Maximize skill, minimize budget</h1>
          <p>Our modern courses across a range of in-demand skills will give you the knowledge you need to live the life you want.</p>
          <button type='button'>Get Started</button>
        </div>
        <div className='app__header-image'>
          <img src={srcImage} alt="woman picture" />
        </div>
      </div>
      <div className='app__cards'>
        <CustomCard />
        <Cards text="Learn the latest animation techniques to create stunning motion design and captivate your audience." definition="Animation" image={Shape}/>
        <Cards text="Create beautiful, usable interfaces to help shape the future of how the web looks." definition="Design" image={Design} />
        <Cards text="Explore critical fundamentals like lighting, composition, and focus to capture exceptional photos." definition="Photography" image={Photography} />
        <Cards text="All you need to know to get started investing in crypto. Go from beginner to advanced with this 54 hour course." definition="Crypto" image={Crypto} />
        <Cards text="A step-by-step playbook to help you start, scale, and sustain your business without outside investment." definition="Business" image={Business} />
      </div>
      <div className='app__footer'>
        <p>skilled</p>
        <button type='button'>Get Started</button>
      </div>
    </div>
  );
}

export default App;
