import './Cards.css';

let Cards = (props) => {
    return(
        <div className='card'>
            <div className='card__icon'>
                <img src={props.image} />
            </div>
            <div className='card__info'>
                <h2>{props.definition}</h2>
                <p>{props.text}</p>
            </div>
            <button type='button'>Get Started</button>
        </div>
    );
}

export default Cards;